import { useState } from "react";
import { Container } from "./styles";
import Button from "../button";

import { Link } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';

const Header = () => {
    const [ menu, setMenu ] = useState(false);

    const wrapper = () => {
        if(menu === true) {
            setMenu(false);
        } else {
            setMenu(true);
        }
    }
    
    return (
        <Container>
            <div className="headerBottom">
                <div className="container">
                    <div className="headerBottomMainWrapper">
                        <div className="row">
                            <div className="logo">
                                <h2>Fulano Fagundes</h2>
                                <small>Nutricionista Funcional</small>
                            </div>
                            <div 
                                onClick={wrapper}
                                className="open"
                            >
                                <MenuIcon 
                                    className="icon" 
                                />
                            </div>
                        </div>
                        <div 
                            className="menuPhone" 
                            style={{
                                height: menu ? '240px' : '0px'
                            }}
                        >
                            <ul >
                                <li>Quem sou?</li>
                                <li>Especialidades</li>
                                <li>Consultas</li>
                                <li>Feedbacks</li>
                                <li style={{marginTop: '10px'}}>
                                    <Link to={"https://wa.me/5581991354850"}>
                                        <Button
                                        value={"Agendar consulta"}
                                        />
                                    </Link>
                                </li>
                            </ul>

                        </div>
                        <div className="menuWeb">
                            <ul >
                                <li>Quem sou?</li>
                                <li>Especialidades</li>
                                <li>Consultas</li>
                                <li>Feedbacks</li>
                                <li className="link">
                                    <Link to={"https://wa.me/5581991354850"} target="_blank">
                                        <Button
                                        value={"Agendar consulta"}
                                        />
                                    </Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default Header;