import styled from "styled-components";

export const Container = styled.div`
    background-color: var(--pink);
    padding: 50px 15px;

    #contact {
        max-width: 700px;
        margin-inline: auto;
    }

    h2, p {
        color: #f1f1f1;
        max-width: 800px;
        padding-top: 40px;
    }

    p {
        margin-top: 0;
        margin-bottom: 40px;
    }

    h4 {
        font-size: 18px;
    }

    h5 {
        font-size: 16px;
    }

    .btn {
        margin-inline: auto;
        width: max-content;
        display: inline-block;
        color: #272c3f;
        padding: 0.75rem 1.2rem;
        border-radius: 1rem;
        cursor: pointer;
        transition: all 400ms ease;
        background-color: #f1f1f1;
        border: 1px solid var(--pink);
        font-size: 16px;
    }

    .btn:hover {
        background: var(--pink);
        color: #f1f1f1;
        border: 1px solid var(--white-color);
    }

    #contact {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .container.contact__container {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .contact__options {
        max-width: 450px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.2rem;
        margin-inline: 10%;
    }

    .contact__option {
        width: 100%;
        background: var(--white-color);
        padding-block: 20px;
        border-radius: 1.2rem;
        text-align: center;
        border: 1px solid transparent;
        transition: all 400ms ease;
        color: var(--pink);
        
    }    

    .contact__option:hover {
        background: transparent;
        color: var(--white-color);
        border: 1px solid #f1f1f1;

        a {
            color: var(--white-color);
            border-bottom: 1px solid #f1f1f1;
        }
    }

    .contact__option-icon {
        width: 35px;
        height: 35px;

        svg {
            color: #c0b596;
        }
    }

    .contact__option a {
        margin-top: 0.7rem;
        display: inline-block;
        font-size: 15px;
        text-decoration: none;
        color: #272c3f;
        border-bottom: 1px solid #272c3f;
    }

    form {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
    }

    input, textarea {
        padding: 1.5rem;
        border-radius: 0.5rem;
        background: transparent;
        border: 2px solid #c0b596;
        resize: none;
        font-size: 16px;
        color: var(--white-color);
    }

    textarea::placeholder,
    input::placeholder {
        color: var(--white-color);
    }

    textarea {
        height: 180px;
    }

    @media screen and (min-width: 600px) {
        .contact__options {
            margin-inline: 20%;
        }
    }
    
    @media screen and (min-width: 1030px) {
        #contact {
            max-width: 1100px;
        }

        .contact__options {
            margin-inline: 0;
        }

        .container.contact__container {
            max-width: 900px;
            display: grid;
            grid-template-columns: 35% 1fr;
        }
    }
`;