import styled from "styled-components";

export const Container = styled.div`
    background-color: var(--light-pink1);
    padding: 370px 15px 100px 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 50px;

    h2 {
        font-size: 30px;
        text-decoration: underline;
    }

    p {
        font-size: 20px;
        max-width: 650px;
        margin-bottom: 50px;
    }

    img {
        width: 250px;
        height: 250px;
        border: 8px solid var(--pink);
        border-radius: 100%;
    }

    @media screen and (min-width: 850px) {
        padding-top: 100px;
    }

    @media screen and (min-width: 1100px) {
        flex-direction: row;
        justify-content: center;
        gap: 100px;
    }
`;