import { Container } from "./styles";
import { Link } from "react-router-dom";
import Button from "../button";

import background from '../../assets/back-nutri.png';
import woman from '../../assets/woman.png';

const Intro = () => {
    return(
        <Container style={{
            backgroundImage: `url(${background})`,
            backgroundColor: 'var(--light-pink2)',
            backgroundPosition: 'top center',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            backgroundAttachment: 'fixed'
        }} >
            <div className="intro1">
                <h1>Fulano Fagundes</h1>
                <h3>Nutricionista Funcional</h3>
                <p>Ajudo voc a alcanar um estilo de vida saudvel, com uma rotina alimentar equilibrada, individualizada e que faa sentido para a correria do seu dia a dia.</p>
            </div>
            <div className="intro2">
                <Link 
                    to={"https://wa.me/5581991354850"} 
                    target="_blank">
                        <Button value={"Agende sua consulta agora"}/>
                </Link>
                <img height="450px" src={woman} alt="Foto de mulher sentada" />
            </div>
        </Container>
    );
}

export default Intro;