import { Container } from "./styles";
import Button from "../button";
import Image from "../../assets/girl.jpg";
// import SupportAgentIcon from '@mui/icons-material/SupportAgent';
// import BalanceIcon from '@mui/icons-material/Balance';
// import GroupsIcon from '@mui/icons-material/Groups';

const Me = () => {
    return(
        <Container>
            <div className="image">
                <img src={Image} alt="Foto de frente" />
            </div>
            <div>
                <h2>Quem sou?</h2>
                <p>Meu nome  Fulano, muito prazer! Sou nutricionista e a minha atuao envolve sade, bem-estar, emagrecimento e sade. Auxilio pessoas a conquistarem um estilo de vida saudvel, tendo uma rotina alimentar equilibrada.</p>
                <Button 
                    value="Vamos agendar sua consulta"
                />
            </div>
        </Container>
    );
}



export default Me;