import React from 'react';
import emailjs from 'emailjs-com';
import { Container } from './styles';
import EmailIcon from '@mui/icons-material/Email';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';

const Contact = () => {

    const sendEmail = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
    
        emailjs.sendForm('service_9qlct3o', 'template_kt6t52l', e.currentTarget, 'dawLGdpCYA1urYPOL')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });

          e.currentTarget.reset();
      };

    return (
        <Container >
            <section id='contact'>
                <h2>Envie-nos um e-mail ou agende sua consulta pelo WhatsApp</h2>

                <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Repellendus nostrum suscipit rerum ipsum dicta cumque optio sit quod ipsam. Repellendus nostrum suscipit rerum ipsum dicta cumque optio sit quod ipsam.</p>

                <div className="container contact__container">
                    <div className="contact__options">
                        <article className="contact__option">
                            <EmailIcon className='contact__option-icon' />
                            <h4>Email</h4>
                            <h5>devparatodos0@gmail</h5>
                            <a href="mailto:devparatodos0@gmail.com">Envie um email</a>
                        </article>

                        <article className="contact__option">
                            <WhatsAppIcon className='contact__option-icon' />
                            <h4>WhatsApp</h4>
                            <h5>(81)99135-4850</h5>
                            <a href="https://wa.me/5581991354850" target='_blank' rel="noreferrer">Envie uma mensagem</a>
                        </article>
                    </div>
                    <form  onSubmit={sendEmail}>
                        <input type="text" name='name' placeholder='Seu nome completo' required />
                        <input type="email" name="email" placeholder="Seu email" />
                        <textarea name="message" placeholder='Sua mensagem' required ></textarea>
                        <button type='submit' className='btn'>Enviar mensagem</button>
                    </form>
                </div>
            </section>
        </Container>
    );
}

export default Contact;