import { Container } from "./styles";
import FavoriteIcon from '@mui/icons-material/Favorite';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import InstagramIcon from '@mui/icons-material/Instagram';

const Footer = () => {
    return(
        <Container>
            <div className="footer">
                <div className="footer__socials">
                    <a href="https://www.linkedin.com/in/luan-silva-b7262a235/" target="blank"><LinkedInIcon /></a>
                    <a href="https://github.com/LuanGitSilva" target="blank"><GitHubIcon /></a>
                    <a href="https://www.instagram.com/bsilva_luan/" target="blank"><InstagramIcon /></a>
                </div>
                <div className="footer__copyright">
                    <small>&copy; Criado com <FavoriteIcon /> por <a href="https://www.instagram.com/bsilva_luan/" target="blank">Luan Silva</a>.</small>
                </div>
            </div>
        </Container>
    );
}

export default Footer;