import { Container } from "./styles";
import Button from "../button";
import Image from "../../assets/talk.jpg";

const Presential = () => {
    return(
        <Container>
            <div className="width">
                <img src={Image} alt="Duas pessoas conversando" />
                <div className="right">
                    <h2>Consulta presencial</h2>
                    <p>A consulta presencial comtempla um bate-papo para compreender objetivos, sintomas e questes comportamentais/alimentares + rastreamento metablico + avaliao da composio corporal com Bioimpedncia Inbody e antropometria, exames biqumicos, planejamento alimentar individualizado, assim como prescrio de suplementao conforme necessidade.A primeira consulta tem durao mdia de 1h30min e as demais cerca de 1h.</p>
                    <Button
                        value="Agende sua consulta presencial"
                    />
                </div>
            </div>
        </Container>
    );
}

export default Presential;