import styled from "styled-components";

export const Container = styled.a`
    border: 2px solid var(--pink);
    padding: 15px;
    border-radius: 50px;
    color: var(--white-color);
    background-color: var(--pink);
    font-weight: bold;
    max-width: 250px;
    text-align: center;
    cursor: pointer;

    :hover {
        color: var(--pink);
        background-color: var(--white-color);
    }
`;