import styled from "styled-components";

export const Container = styled.div`
    background-color: var(--green);
    padding: 150px 15px;
    color: var(--white-color);
    
    
    .width {
        max-width: 600px;
        display: flex;
        flex-direction: column;
        margin-inline: auto;
    }

    img {
        width: 100%;
        margin-inline: auto;
        border-radius: 30px;
        box-shadow: 0 0 50px 10px #00000070;
    }

    .right {
        text-align: center;

        h2 {
            font-size: 30px;
            margin-top: 50px;
        }

        p {
            font-size: 20px;
            margin-bottom: 50px;
            text-align: left;
        }
    }

    @media screen and (min-width: 1200px) {
        .width {
            max-width: 1200px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 40px;
        }
    }
`;