import styled from "styled-components";

export const Container = styled.div`
    padding: 100px 15px;
    margin-inline: auto;

    h2 {
        text-align: center;
        margin-bottom: 60px;
        font-size: 30px;
    }

    .display {
        margin-inline: auto;   
    }

    .boxes {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    
    .box1 {
        margin-top: 5px;
        border: 2px solid var(--btn-color);
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        cursor: pointer;

        p {
            font-size: 20px;
            font-weight: bold;
        }
    }

    .box2 {
        background-color: var(--green);
        color: var(--white-color);
        padding-inline: 10px;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        overflow: hidden;
        transition: ease-in-out .3s;

        p {
            font-size: 20px;
            text-align: center;
        }
    }

    @media screen and (min-width: 950px) {
        .display {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 10px;
            max-width: 1300px;
        }
    }
`;