import { Container } from "./styles";
import Button from "../button";
import Image from "../../assets/online.jpg";

const Online = () => {
    return(
        <Container>
            <div className="width">
                <div className="left">
                    <h2>Consulta online</h2>
                    <p>A consulta online contempla um encontro via Zoom, alm de um bate-papo para compreender objetivos e questes comportamentais/alimentares + rastreamento metablico + avaliao por fotos/peso/medidas, exames bioqumicos, planejamento alimentar individualizado e suplementao conforme necessidade.A consulta tem durao mdia de 1h30min a 1h.</p>
                    <Button
                        value="Agende sua consulta online"
                    />
                </div>
                <img src={Image} alt="Pessoa em escritrio" />
            </div>
        </Container>
    );
}

export default Online;