import styled from "styled-components";

export const Container = styled.div`
    position: fixed;
    top: 0;
    width: 100%;
    color: #e0e0e0;
    font-size: 16px;
    z-index: 999;
    
    background-color: var(--white-color);
    color: var(--pink);

    ul {
        list-style-type: none;
        padding: 0;
    }
    
    .headerBottom {
        padding-inline: 20px;
    }

    .row {
        padding-block: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .logo {
        display: flex;
        flex-direction: column;
        cursor: pointer;
        font-weight: bold;
        
        h2 {
            font-size: 28px;
            text-decoration: underline;
            margin: 0;
        }

        small {
            text-decoration: none;
            font-size: 20px;
        }
    }

    .open {
        cursor: pointer;
        width: 35px;
        height: 35px;

        .icon {
            height: 100%;
            width: 100%;
        }
    }

    .menuWeb {
        display: none;
    }

    .menuPhone {
        width: 100%;
        overflow: hidden;
        transition: ease-in-out .4s;

        ul {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            height: auto;

            li {
                font-size: 18px;
            }

            li:hover {
                color: #c0b596;
                cursor: pointer;
            }
        }
    }

    @media screen and (min-width: 900px) {
        position: initial;
        top: initial;

        .headerContact {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            border-bottom: 1px solid #1c6077;
        }

        .row {
            border-top: none;

            .logo {
                width: auto;
                font-size: 22px;
            }

            .open {
                display: none;
            }
        }

        .menuPhone {
            display: none;
        }

        .menuWeb {
            display: block;
            border-top: none;
            align-items: center;

            ul {
                display: flex;
                flex-direction: row;
                justify-content: end;
                align-items: center;
                gap: 5px;

                li {
                    border-right: 1px solid #e0e0e0;
                    padding-right: 5px;
                }
            
                li:hover {
                    color: var(--green);
                    cursor: pointer;
                }

                .link {
                    border: none;
                }
            }
        }

        .headerBottomMainWrapper {
            display: flex;
            justify-content: space-between;
            align-items: center; 
            height: 100px;
        }
    
        .headerTop {
            max-width: 1110px;
            margin-inline: auto;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            border-bottom: 1px solid #1c6077;
        }

        .headerBottomMainWrapper {
            max-width: 1110px;
            margin-inline: auto;
        }

        .headerContact {
            width: 60%;
            border: none;
        }
    }
    
    @media screen and (min-width: 1100px) {
        .menuWeb {
            ul {
                gap: 15px;

                li {
                    padding-right: 15px;
                    font-size: 20px;
                }
            }
        }
    }
`;