import styled from "styled-components";

export const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-inline: auto;
    padding: 30px 50px;
    color: var(--white-color);
    background-color: var(--green);
    
    .footer {
        display: flex;
        flex-direction: column;
        gap: 5px;
        max-width: 800px;
    }

    a {
        text-decoration: none;
        color: var(--white-color);
    }

    .footer__copyright {
        small {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 4px;
            font-size: 16px;
        }
    }

    .footer__socials {
        display: flex;
        justify-content: center;

        a {
            padding-inline: 5px;
        }
    }

    @media screen and (min-width: 1030px) {
        .footer {
            flex-direction: row;
            gap: 100px;
            max-width: 800px;
        }
    }
`;