import styled from "styled-components";

export const Container = styled.div`
    height: 550px;
    padding-top: 95px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-inline: 20px;

    .intro1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 700px;
        text-align: center;

        h1 {
            font-size: 48px;
            margin: 30px 0 10px 0;
        }

        h3 {
            font-size: 30px;
            margin: 0;
        }

        p {
            font-size: 22px;
            font-weight: 600;
            margin: 15px 0 30px 0;
        }
    }

    .intro2 {
        display: flex;
        flex-direction: column;
        align-items: center;

        a {
            margin-bottom: 15px;
        }
    }

    @media screen and (min-width: 850px) {
        flex-direction: row;
        justify-content: center;
        gap: 30px;

        .intro1{
            h1 {
                margin-top: 0;
            }

            p {
                margin-top: 30px;
            }
        }

        .intro2 {
            margin-bottom: -270px;
        }
    }
`;